@tailwind base;
@tailwind components;
@tailwind utilities;

html, body {
  height: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.calculator {
  max-width: 600px;
}

/* 이거하니까 왜 다 날라ㅏ가지... */
@layer components {
  .button {
    max-width: 150px;
    max-height: 120px;
    @apply relative m-1.5 rounded-2xl flex items-center justify-center cursor-pointer text-4xl text-white font-medium bg-gray-700;
  }

}